@import '/src/components/GlobalStyles/Responsive.scss';

//slider su dung text
.text-slider {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: var(--black);
  color: var(--white);
  height: 36px;
  padding: 8px;
  font-size: 1.3rem;
  font-weight: normal;
}

//Slider su dung hinh anh

.image-slider {
  position: relative;
  cursor: pointer;
  user-select: none;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .custom-dots {
    bottom: 20px;
  }
}

// slider san pham

.products-slider {
  margin: 30px auto 0 auto;
}

// promition-slider
.promition-image {
  width: 100%;
}
